import React from "react";
import Navbar from "./Navbar";
import "./Navbar.css";
const Header = () => {
  var TxtRotate = function (el, toRotate, period) {
    this.toRotate = toRotate;
    this.el = el;
    this.loopNum = 0;
    this.period = parseInt(period, 10) || 2000;
    this.txt = "";
    this.tick();
    this.isDeleting = false;
  };

  
  TxtRotate.prototype.tick = function () {
    var i = this.loopNum % this.toRotate.length;
    var fullTxt = this.toRotate[i];

    if (this.isDeleting) {
      this.txt = fullTxt.substring(0, this.txt.length - 1);
    } else {
      this.txt = fullTxt.substring(0, this.txt.length + 1);
    }

    this.el.innerHTML = '<span className="wrap">' + this.txt + "</span>";

    var that = this;
    var delta = 300 - Math.random() * 100;

    if (this.isDeleting) {
      delta /= 2;
    }

    if (!this.isDeleting && this.txt === fullTxt) {
      delta = this.period;
      this.isDeleting = true;
    } else if (this.isDeleting && this.txt === "") {
      this.isDeleting = false;
      this.loopNum++;
      delta = 500;
    }

    setTimeout(function () {
      that.tick();
    }, delta);
  };

  window.onload = function () {
    var elements = document.getElementsByClassName("txt-rotate");
    for (var i = 0; i < elements.length; i++) {
      var toRotate = elements[i].getAttribute("data-rotate");
      var period = elements[i].getAttribute("data-period");
      if (toRotate) {
        new TxtRotate(elements[i], JSON.parse(toRotate), period);
      }
    }
    // INJECT CSS
    var css = document.createElement("style");
    css.type = "text/css";
    css.innerHTML = ".txt-rotate > .wrap { border-right: 0.08em solid #666 }";
    document.body.appendChild(css);
  };
  return (
    <>
      <div id="header" className="contianer-fluid  header-gradient">
        <Navbar />
        <div id="darky" className="container darky">
          <h1>
            This pen is
            <span
              className="txt-rotate"
              data-period="2000"
              data-rotate='[ " nerdy.", " simple.", " pure JS.", " pretty.", " fun!" ]'
            ></span>
          </h1>
        </div>
        <div id="formcont" className="d-flex justify-content-center container formcont">
          <form className="d-flex  bg-transparent">
            <input
              className="form-control searchform me-2 "
              type="search"
              placeholder="Search"
              aria-label="Search"
            />
            <button className="btn searchbtn " type="submit">
              Search
            </button>
          </form>
        </div>
      </div>
    </>
  );
};

export default Header;
